/* ========== CORE TAG STYLES ========== */
html,
body {
  background: radial-gradient(#262626, black);
  position: relative;
  margin: 0;
  min-height: 100vh;
}
body::after {
  content: '';
  display: block;
  height: 20rem; /* Set same as footer's height */
}
/* ===================================== */

/* ========== NAVBAR STYLES ========== */
.navNoBackground {
  background: none;
  border: none;
  backdrop-filter: none;
  height: 5rem;
}
.navSemiBackground {
  background-color: rgba(13, 14, 17, 0.8);
  border-color: rgb(32, 34, 45);
  border-bottom-width: 1px;
  border-bottom-style: solid;
  backdrop-filter: blur(18px);
  height: auto;
}
/* =================================== */

/* ========== FOOTER STYLES ========== */
.footerBackgroundStyle {
  background-color: rgba(13, 14, 17, 0.8);
  border-color: rgb(32, 34, 45);
  border-top-width: 1px;
  border-top-style: solid;
  backdrop-filter: blur(18px);
}

.footerPositionStyle {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 15rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

@media screen and (max-width: 991px) {
  .footerPositionStyle {
    height: 24rem;
  }
  body::after {
    height: 28rem; /* Set same as footer's height */
  }
}

.colContainerStyle {
  display: flex;
  flex-direction: column;
  height: 11rem;
  width: 100%;
}
/* =================================== */

/* ========== Container Styles ========== */
.containerStyle {
  max-height: 100%;
  padding-top: 5rem;
}
/* ====================================== */

/* ========== Custom Styles ========== */
.linkStyle {
  text-decoration: none;
  color: rgb(166, 170, 191);
  transition: 0.3s;
  margin-bottom: 0.1rem !important;
}

.linkStyle:hover,
.iconLinkStyle:hover {
  color: white;
  cursor: pointer;
}

.specialLinkStyle,
.specialLinkStyle > a {
  text-decoration: none;
  color: rgb(166, 170, 191);
  transition: 0.3s;
}

.specialLinkStyle:hover,
.specialLinkStyle > a:hover,
.specialLinkStyle:focus,
.specialLinkStyle > a:focus {
  text-decoration: none;
  color: white;
}

.iconLinkStyle {
  text-decoration: none;
}
/* =================================== */

/* ========== Home Page Styles ========== */
.meContainer {
  justify-self: center;
  padding: 3em;
}
.mePhoto {
  margin: auto;
  height: 16em;
  width: 16em;
  border-radius: 8em;
  box-shadow: 0 1em 1.5em 0 rgba(0, 0, 0, 0.1), 0 1em 2em 0 rgba(0, 0, 0, 0.2);
}
.hello {
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  padding-bottom: 1em;
}
.smallBlurb {
  display: inline;
  color: white;
  font-size: 1em;
  font-weight: bold;
}
.blurb-p:nth-child(even) {
  color: #55cc69;
}
.typedStyles {
  color: #cfcfcf;
  font-size: 1em;
  font-weight: bold;
  padding-bottom: 0.5em;
  display: inline;
}

/* ========== About Page ========== */
.page-content {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.aboutBox {
  width: 100%;
  margin: auto;
  padding: 2rem;
  border-radius: 0.3rem;
}
.textContainer {
  margin: auto 1rem auto 1rem;
}
.mainTitle {
  font-weight: 400;
  text-align: left;
  font-size: 3rem;
  color: white;
  margin: 2rem auto 2rem auto;
}
.subTitle {
  text-align: left;
  font-weight: 400;
  color: #55cc69;
  margin: 0 auto 1rem auto;
}
.contentText {
  text-align: left;
  color: white;
}
.aboutYouShow {
  text-decoration: none;
  color: #ccc;
  transition: 200ms;
}
.aboutYouShow:hover {
  color: #eee;
  cursor: pointer;
}
.theShort,
.technologyInformation {
  margin: 0 auto 2rem auto;
}
.theLong {
  margin: 0 auto 0 auto;
}
.smallPad {
  padding-top: 1rem;
}
.subsubTitle {
  text-align: left;
  font-size: 1.3rem;
  font-weight: 400;
  color: #55cc69;
}
/* ================================ */

/* ========== Contact Page ========== */
.page-content {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.contactForm {
  width: 100%;
  margin: auto;
  padding: 2rem;
  border-radius: 0.3rem;
}
.textContainer {
  margin: auto 0.5rem auto 0;
}
.mainTitle {
  text-align: left;
  font-size: 2rem;
  margin: 0 auto 1rem auto;
  color: #55cc69;
}
.input-box {
  padding: 0.5rem;
  transition: background-color 0.1s linear;
  color: white;
  background-color: rgba(0, 0, 0, 0);
}
.input-box:focus {
  padding: 0.5rem;
  color: black;
  background-color: #bbb;
}
.input-box::placeholder {
  transition: 0.1s;
  color: #ccc;
}
.input-box:focus::placeholder {
  color: #444;
}
.input-container {
  padding: 0.5rem 0 0.5rem 0;
}
.text-area {
  min-height: 5rem !important;
  max-height: 20rem;
  height: auto;
  overflow: auto;
  transition: none;
  resize: vertical;
}
.form-utility {
  display: grid;
  grid-template-columns: 1fr 2fr;
  padding-top: 0.5rem;
}
.error-message {
  text-align: left;
  padding-left: 1.5rem;
  align-self: center;
  margin: 0;
}
.button-container {
  width: 100%;
  height: 100%;
}
.submit-button {
  width: 100%;
  border-radius: 0.2rem;
  background-color: #55cc69;
  border-color: #55cc69;
  height: 2.5rem;
}
.spinner-itself {
  justify-self: center;
  align-self: center;
  margin: 0.25rem;
}
/* ================================== */
